@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);


.thumb-container{display:flex;flex-direction:column;align-items:center;justify-content:center;padding:1.5rem .2rem;margin:.8rem .3rem;border:1px solid #efefef;border-radius:2rem;min-width:160px;min-height:268px;text-align:center;box-shadow:0 3px 15px rgba(0,0,0,.089);transition:all .2s linear}.thumb-container:hover{-webkit-transform:translateY(-12px);transform:translateY(-12px)}.thumb-container:hover img{width:160px;height:160px;-webkit-animation:shake 1s linear infinite;animation:shake 1s linear infinite}.thumb-container .number{border-radius:1rem;padding:.2rem .4rem;background-color:rgba(255,255,255,.3)}.thumb-container img{width:120px;height:120px;transition:all .2s linear}.thumb-container small{text-transform:capitalize}.thumb-container.rock{background-image:linear-gradient(to top, #283048 0%, #859398 100%);color:#fff}.thumb-container.ghost{background-image:linear-gradient(to top, #706c86 0%, #706c86 100%);color:#fff}.thumb-container.electric{background-image:linear-gradient(to right, #fceabb 0%, #f8b500 100%)}.thumb-container.bug{background-image:linear-gradient(to top, #16a085 0%, #f4d03f 100%);color:#fff}.thumb-container.poison{background-image:linear-gradient(to top, #42275a 0%, #734b6d 100%);color:#fff}.thumb-container.normal{background-image:linear-gradient(-225deg, #e3fdf5 0%, #ffe6fa 100%)}.thumb-container.fairy{background-image:linear-gradient(to top, #ff9a9e 0%, #fecfef 99%, #fecfef 100%)}.thumb-container.fire{background-image:linear-gradient(315deg, #fe8c00 0%, #f83600 74%);color:#fff}.thumb-container.grass{background-image:linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%)}.thumb-container.water{background-image:linear-gradient(120deg, #0575e6 0%, #021b79 100%);color:#fff}.thumb-container.ground{background-image:linear-gradient(315deg, #603813 0%, #b29f94 74%);color:#fff}.thumb-container.psychic{background-image:linear-gradient(315deg, #de6161 0%, #2657eb 74%);color:#fff}.thumb-container.fighting{background:linear-gradient(to right, #833ab4, #fd1d1d, #fcb045);color:#fff}.thumb-container.ice{background-image:linear-gradient(120deg, #89f7fe 0%, #66a6ff 100%)}.thumb-container.dragon{background-image:linear-gradient(120deg, #870000 0%, #190a05 100%);color:#fff}.thumb-container.dark{background-image:linear-gradient(120deg, #232526 0%, #414345 100%);color:#fff}.thumb-container.steel{background-image:linear-gradient(120deg, #8e9eab 0%, #eef2f3 100%)}.thumb-container.flying{background:linear-gradient(to right, #007991, #78ffd6);color:#fff}@-webkit-keyframes shake{0%{-webkit-transform:translate(0px, 0px) rotate(0deg);transform:translate(0px, 0px) rotate(0deg)}25%{-webkit-transform:translate(2px, 2px) rotate(2deg);transform:translate(2px, 2px) rotate(2deg)}50%{-webkit-transform:translate(0px, 0px) rotate(2deg);transform:translate(0px, 0px) rotate(2deg)}75%{-webkit-transform:translate(-2px, -2px) rotate(2deg);transform:translate(-2px, -2px) rotate(2deg)}100%{-webkit-transform:translate(0px, 0px) rotate(0deg);transform:translate(0px, 0px) rotate(0deg)}}@keyframes shake{0%{-webkit-transform:translate(0px, 0px) rotate(0deg);transform:translate(0px, 0px) rotate(0deg)}25%{-webkit-transform:translate(2px, 2px) rotate(2deg);transform:translate(2px, 2px) rotate(2deg)}50%{-webkit-transform:translate(0px, 0px) rotate(2deg);transform:translate(0px, 0px) rotate(2deg)}75%{-webkit-transform:translate(-2px, -2px) rotate(2deg);transform:translate(-2px, -2px) rotate(2deg)}100%{-webkit-transform:translate(0px, 0px) rotate(0deg);transform:translate(0px, 0px) rotate(0deg)}}.detail-wrapper{display:flex;flex-direction:column;width:100%}.detail-wrapper button{color:#161616;padding:.5rem;margin-top:1rem;border:none;border-radius:.2rem;cursor:pointer;background-color:rgba(0,0,0,.185)}.detail-wrapper button:hover{background-color:red;background-image:linear-gradient(315deg, #ff0000 0%, #ffed00 74%)}.load-more{background-color:#76daff;background-image:linear-gradient(315deg, #76daff 0%, #fcd000 74%);border-radius:6px;border:1px solid #c6c6c6;color:#444;padding:.5rem 1.5rem;min-width:20%;margin-top:1rem;font-family:"Satisfy",cursive}.pokeinfo{margin:0 25px;font-family:"Satisfy",cursive;background-image:linear-gradient(180deg, #2af598 0%, #009efd 100%)}.pokeinfo:hover{background-image:linear-gradient(-20deg, #ddd6f3 0%, #faaca8 100%, #faaca8 100%)}
body{margin:0;font-family:"Satisfy",cursive;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-image:linear-gradient(to right, #eea2a2 0%, #bbc1bf 19%, #57c6e1 42%, #b49fda 79%, #7ac5d8 100%)}h3{text-decoration:underline;margin-bottom:.2rem}.app-container{display:flex;flex-direction:column;align-items:center;justify-content:center;min-height:100vh;padding:3rem 1rem}.app-container h1{width:0ch;color:#2610ec;overflow:hidden;white-space:nowrap;-webkit-animation:text 4s steps(15) infinite alternate;animation:text 4s steps(15) infinite alternate}@-webkit-keyframes text{0%{width:0ch}50%{width:15ch;color:#da3705}}@keyframes text{0%{width:0ch}50%{width:15ch;color:#da3705}}.pokemon-container{display:flex;flex-direction:column;align-items:center;justify-content:center}.all-container{display:flex;flex-wrap:wrap;align-items:center;justify-content:center}
